* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country__list {
  display: flex;
  align-items: center;
}

.country__flag {
  width: 50px;
  margin-right: 15px;
}

.country__properties {
  font-weight: 900;
  margin-right: 15px;
}

.country__properties--description {
  font-weight: 500;
  margin-left: 10px;
}
